export class CreatCustExplCondBean {
    explortFlag: any; // 高级搜索下载：1.我的客户，2.客户群
    postId: any;
    displayTarget: string;
    requestType: string;//服务请求类型
    userName: string;//用户名称
    orgName: string;//机构名称
    expCondName: string;//探索条件名称
    statusId: string;//状态标志
    marketFlag: string;//是否营销界面探索客户
    expCondId:string;//探索条件编号 no
    modelScale: string;//模型客户比例 no
    custHoldProdLists: string;//7.客户持有产品标识
    requestMap: RequestMap = new RequestMap();
    
};

export class RequestMap {
    qryScopeMap: QryRange = new QryRange();//1.查询范围
    custInfo: CustInfo = new CustInfo();//客户基本信息
    indiCustomer: IndiCustomer = new IndiCustomer();//个人客户属性
    businessInfo: BusinessInfo = new BusinessInfo();//经营信息
    creditAttrMap: CreditAttrMap = new CreditAttrMap();//信贷业务
    custHoldProdAttrList: any;//8.客户产品属性列表
    custLabelAttrList: any;//9.客户标签属性列表
    custGroupAttrList: any;//10.客户群属性列表
    ftpAttrList: any;//11.FTP利润属性列表
    thirdPartyEvaluate: ThirdPartyEvaluate = new ThirdPartyEvaluate();//第三方评价
}
//1.查询范围
export class QryRange {
    custGrouId: string;//客户群标号
    sysModelId: string;//系统模型 no
    sysModelName: string;//系统模型名称 no
    startTime: string;//查询时间基点
    custArea: string;//查询客户范围类型
    ownerOrgIds: any;//主联系人机构标识
    ownerOrgNames: any;//主联系人机构名称
    ownerIds: any;//主联系人标识
    ownerNames: any;//主联系人名称
    assActorOrgIds: any;//协储员机构标识
    assActorOrgNames: any;//协储员机构名称
    assActorIds: any;//协储员标识
    assActorNames: any;//协储员名称
    oldCampaignIds: any = [
        {
            oldCampaignId: '',//近期活动已覆盖客户活动ID
            oldCampaignName: '',//近期活动已覆盖客户活动名称
            oldCampaignType: '',//近期活动已覆盖客户活动选择类型
        }
    ];
}

// 客户基本信息
export class CustInfo {
    custType: string;                    //客户类型
    custNo: string;                       //客户号  
    custName: string;                    //客户名称 
    custAttr: string;                      //客户属性
    isAgriculture: string;             //涉农属性  
    // cycle: string;                     //客户生命周期 
    crdRateType: string;               //信用评分类型
    crdRateStart: string;             //信用评级起
    crdRateEnd: string;             //信用评级止  
    // afterLoanRateType: string;     //贷后评分类型    
    creatTimeStart: string;       //开户日期起   
    creatTimeEnd: string;        //开户日期止   
    // isCancel: string;           //是否注销    
    IDCardType: string;        //证件类型    
    IDCardNo: string;         //证件号码   
    tel: string;             //联系号码
    turnIndustry: string;          // 投向行业
    // turnIndustry2: string;          // 投向行业
    // turnIndustry3: string;          // 投向行业
    // turnIndustry4: string;          // 投向行业
}

