		// 证件类型
		this.iDCardOpt3 = this.code["IDCardOpt"];
		this.iDCardOpt1 = this.code["IDCardOpt1"];
		this.iDCardOpt2 = this.code["IDCardOpt2"];
		// 涉农属性
