	/**
	 * *  打开探索条件、选择成熟模板详情回显开始
	*/
	//1.（探索条件的详情回显）确定按钮
	//2.确定按钮(成熟模板的详情回显)
	tempDetail() {
		this.creatCustExplCondBean = new CreatCustExplCondBean();
		let param = { "exploreConditionTempId": this.exploreConditionTempId };
		this.httpService.queryScreenCustomerTemplateDetail(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				// this.creatCustExplCondBean.expCondName = data.exploreConditionName;//模板名称
				this.creatCustExplCondBean.statusId = data.statusId;//模板状态     
				if (data.requestMap) {
					//4.客户基本属性筛选
					if (data.requestMap.custInfo.creatTimeStart || data.requestMap.custInfo.creatTimeEnd) {
						this.creatStaDate = new Date(data.requestMap.custInfo.creatTimeStart);//开始时间
						this.creatEndDate = new Date(data.requestMap.custInfo.creatTimeEnd);//结束时间
					}
					this.creatCustExplCondBean.requestMap.custInfo = data.requestMap.custInfo;
					this.radioBtn();
					// 5-1个人客户属性筛选
					if (data.requestMap.indiCustomer) {
						if (data.requestMap.indiCustomer.birthDateStart || data.requestMap.indiCustomer.birthDateEnd) {
							this.birthDateFrom = new Date(data.requestMap.indiCustomer.birthDateStart);//开始时间
							this.birthDateTo = new Date(data.requestMap.indiCustomer.birthDateEnd);//结束时间
						}
						this.creatCustExplCondBean.requestMap.indiCustomer = data.requestMap.indiCustomer;
					}
					// 5-2经营信息筛选
					if (data.requestMap.businessInfo) {
						this.creatCustExplCondBean.requestMap.businessInfo = data.requestMap.businessInfo;
					}
					// 7.客户持有信息筛选
					if (data.requestMap.custHoldProdLists) {
						this.creatCustExplCondBean.custHoldProdLists = data.requestMap.custHoldProdLists;
					}
					//8.客户产品属性筛选
					if (data.requestMap.custHoldProdAttrList) {
						data.requestMap.custHoldProdAttrList.forEach((items, i) => {
							items.forEach((item, ind) => {
								this.custHoldProdAttrList = data.requestMap.custHoldProdAttrList;
								if (JSON.stringify(item) != "{}") {
									this.proChange(this.custHoldProdAttrList, item.prodCateId, i, ind);
									this.subProChange(this.custHoldProdAttrList, item.subProdCateId, i, ind, item.prodCateId);
								}

							})
						})
					}
					//9.客户标签属性筛选
					if (data.requestMap.custLabelAttrList) {
						this.custLabelAttrList = data.requestMap.custLabelAttrList;
						this.custLabelAttrList.forEach((items, i) => {
							items.forEach((item, ind) => {
								if (item.labelName) {
									//将回显的值在进行一次模糊查询
									this.cusTagName(i, ind, item.labelName);
									//设置超时操作，防止异步操作造成的干扰
									setTimeout(() => {
										if (this.custLabelAttrList[i][ind]['cusTagNameOpt']) {
											this.custLabelAttrList[i][ind]['cusTagNameOpt'].forEach(seleListItem => {
												//根据回显的客户标签名（labelName），去cusTagNameOpt数组里，去匹配对应数组进行ngModel绑定
												//（联想查询绑定的ngModel是个对象）
												if (item.labelName == seleListItem.labelName) {
													if (this.custLabelAttrList[i][ind]['selectNameOpt']) {
														this.custLabelAttrList[i][ind]['selectNameOpt'] = seleListItem;
													} else {
														this.custLabelAttrList[i][ind]['selectNameOpt'] = {};
														this.custLabelAttrList[i][ind]['selectNameOpt'] = seleListItem;
													}
												}
											})
										}
									}, 200)
								}
							})
						})
					} else {
						this.custLabelAttrList = [[]];
					}

					//10.客户群属性筛选
					if (data.requestMap.custGroupAttrList) {
						this.custGroupAttrList = data.requestMap.custGroupAttrList;
						this.custGroupAttrList.forEach((items, i) => {
							items.forEach((item, ind) => {
								if (item.custGrouName) {
									//将回显的值在进行一次模糊查询
									this.cusGroupName(i, ind, item.custGrouName);
									//设置超时操作，防止异步操作造成的干扰
									setTimeout(() => {
										if (this.custGroupAttrList[i][ind]['cusGroupNameOpt']) {
											this.custGroupAttrList[i][ind]['cusGroupNameOpt'].forEach(seleListItem => {
												//根据回显的客户群属性名（custGrouName），去cusGroupNameOpt数组里，去匹配对应数组进行ngModel绑定
												//注意：（联想查询绑定的ngModel是个对象）
												if (item.custGrouName == seleListItem.groupName) {
													if (this.custGroupAttrList[i][ind]['selectNameOpt']) {
														this.custGroupAttrList[i][ind]['selectNameOpt'] = seleListItem;
													} else {
														this.custGroupAttrList[i][ind]['selectNameOpt'] = {};
														this.custGroupAttrList[i][ind]['selectNameOpt'] = seleListItem;
													}
												}
											})
										}
									}, 200)
								}
							})
						})
					} else {
						this.custGroupAttrList = [[]]
					}

					//11.FTP利润属性筛选
					if (data.requestMap.ftpAttrList) {
						this.ftpAttrList = data.requestMap.ftpAttrList;
					} else {
						this.ftpAttrList = [[]];
					}
				}
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '探索条件详情', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '探索条件详情', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '探索条件详情', detail: '调用服务失败！' });
		})
	}
